:root{
    --hsize:50px;
    --ssize:500px;
}

.boxes{
    display:flex;
}

.o_box{
    width:50vw;
    height:100vh;
    background-color: blue;

    display:flex;
    justify-content: center;
    align-items: center;

    cursor:pointer;
}

.x_box{
    width:50vw;
    height:100vh;
    background-color: red;

    display:flex;
    justify-content: center;
    align-items: center;

    cursor:pointer;
}

.selection{
    position:absolute;
    top:50px;
    left:50%;
    transform: translateX(-50%);
    font-size: var(--hsize);
    color:white;
}

.o{
    font-size:var(--ssize);
    color:white;
}

.x{
    font-size:var(--ssize);
    color:white;
}
